          goDataBaseSearch(type,page){
            const _this =this;
            const tagLens = this.resultKeyDatas.length;
            let tempStr = "";
            if(tagLens>0){
              for (let i=0;i<tagLens;i++){
                tempStr += `${$.trim(this.resultKeyDatas[i])},`;
              }
            }else{
              tempStr = _this.searchSingle;
            }
            console.log("全部:",_this.searchSingle);
            if(_this.searchSingle!=="" && _this.searchSingle!=="null" && _this.searchSingle!==null){
              this.$http.post("api1.0/hotkeys/",{
                keyword:_this.searchSingle
              }).then((res)=>{

              },(err)=>{

              });
            }
            if(tempStr!=='' || this.resultKeyDatas.length>0){
              let searchUrl = '';
              if(_this.isAll === 0){//搜索全部
                if(tempStr.indexOf(',')>0){
                  searchUrl = `${Api.SEARCH}?tag=${tempStr}`;
                }else{
                  searchUrl = `${Api.SEARCH}?q=${tempStr}`;
                }

              }else{//搜索部分
                if(tempStr.indexOf(',')>0){
                  searchUrl = `${Api.SEARCH}?tag=${tempStr}&search_type=2`;
                }else{
                  searchUrl = `${Api.SEARCH}?q=${tempStr}&search_type=2`;
                }

              }
              //这里就执行搜索
              this.$http.get(searchUrl,{
                params:{
                  page:page,
                  page_size:_this.ziPageSize,
                }
              }).then((res)=>{
                _this.ziTotalCount = res.data.data.total_count;
                if(res.status === 200 && res.data.data!==null){
                  let tempArr = [];
                  tempArr = res.data.data.detail.hits.hits;
                  tempArr.length>0 ? _this.isNoData = false:_this.isNoData = true;
                  if(type===0){
                    _this.searchData = tempArr;
                  }
                  //下拉刷新
                  else if(type===1){
                    _this.searchData=tempArr.splice(0,_this.ziPageSize);
                  }else if(type === 2){ //上拉加载
                    _this.searchData = _this.searchData.concat(tempArr);
                  }
                  logUtil.printLog("资料库 search success ",res);

                }else{
                  _this.isNoData = true;
                }
              },(err)=>{
                _this.isNoData = true;
                _this.$store.dispatch('hideLoading');
                logUtil.printLog("search err",err);
              })
            }else{
              //这里就要显示tag标签
