            getMyTags(){
              const _this = this;
              this.$http.get(Api.TAGS).then((res)=>{
                _this.initMyTags(res);
              },(err)=>{
              });
            },
            initMyTags(res){
                  let timer = null;
                  const _this = this;
                  if(res.status === 200 && res.data.data!==null){
                      this.searchKeyDatas =res.data.data.detail;
                      //延时：解决了jq动态绑定元素时候点击事件的失效
                      timer = setTimeout(function () {
                        _this.toggleSearch();
//                        _this.toggleTabBar();
                      },1000);
                  }
            },
            initTabs(){
              const _this = this;
              $('.search-tab-item').each(function (index,item) {
                $(item).on('click',function () {
                  _this.isShowComponent = index;
                });
              });
            },
            showMsg(str){
              this.$toast({
                message: str,
                duration: 2000,
              });
            },
            /**
             * 超过三个item就不可以点击了
             *
             */
            filterAboveThree(){
                let len = this.resultKeyDatas.length;
                let lis =  $('.search-item');
               this.isChoiceTags(lis,len);
            },
            /**
             *根据点击删除的tag来判断是否恢复选择的tag
             * */
            isChoiceTags(lis,len){
                if(len>1){
                    this.$toast({
                        message: '所选tag不能超过二个哦!',
                        duration: 2000,
                    });
                    $(lis).each(function (index,item) {
                        $(item).addClass('disabled');
                    });
                }else{
                    $(lis).each(function (index,item) {
                        $(item).removeClass('disabled');
                    });
                }
            },

            /**
             * 动态切换选择的tag标签
             */
            toggleSearch(){
                const _this = this;
                let lis =  $('.search-item');
                $(lis).each(function (index,item) {
                    item.onclick = function () {
                       _this.searchSingle = '';
                        $(this).addClass('actived');
                        _this.singleKeyDatas.push($(this).html());
                        _this.singleKeyDatas.length <=0?_this.isShowSearchItems =false : _this.isShowSearchItems = true;
                        _this.resultKeyDatas.push($(this).html());
                        _this.filterAboveThree();
                        $(this).addClass('disabled');
                    };
                });
            },
            /**
             * 删除对应的tag
             * @param index
             */
            del(index,singleItem){
                const _this = this;
                let lis =  $('.search-item');
                $(lis).each(function (index,item) {
                  if(item.innerHTML === singleItem){
                     item.className = 'search-item';
                  }
                });
                _this.resultKeyDatas.splice(index,1);
                let len = _this.resultKeyDatas.length;
                if(len===0){
                  _this.isShowSearchItems = false;
                }
                _this.isChoiceTags(lis,len);
            }
        }
    }
</script>
